		stroke-dashoffset: -225;
	}

	78% {
		stroke-dasharray: 40 400;
		stroke-width: 30;
		stroke-dashoffset: -395;
	}

	86%,
	to {
		stroke-dasharray: 0 440;
		stroke-width: 20;
		stroke-dashoffset: -440;
	}
}

footer {
	align-self: center;
	display: flex;
	align-items: center;
	gap: 8px;
	border-radius: 10px;
	padding: 16px;
	margin: 0 16px 16px 16px;
}
